<template>
    <v-dialog v-model="isOpen" max-width="400px">
      <v-card>
        <v-card-title class="headline" v-if="action === 1">Activar Item?</v-card-title>
        <v-card-title class="headline" v-if="action === 2">Desactivar Item?</v-card-title>
        <v-card-text>
          Estás a punto de <span v-if="action === 1">Activar</span>
          <span v-if="action === 2">Desactivar</span> el ítem {{ localCategory.categorY_NAME }}.
        </v-card-text>
        <v-card-actions>
          <v-btn v-if="action === 1" color="blue" dark class="mb-2" @click="activar">Activar</v-btn>
          <v-btn v-if="action === 2" color="orange" dark class="mb-2" @click="desactivar">Desactivar</v-btn>
          <v-btn color="red" @click="close">Cancelar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </template>
  
  <script>
  export default {
    props: {
      modelValue: {
        type: Boolean,
        required: true,
      },
      category: {
        type: Object,
        default: () => ({ pK_CATEGORY: null, categorY_NAME: ''}),
      },
      action: {
        type: Number,
        required: true,
      },
    },
    data() {
      return {
        isOpen: this.modelValue,
        valid: false,
        localCategory: { ...this.category },
      };
    },
    watch: {
      modelValue(newValue) {
        this.isOpen = newValue;
      },
      category: {
        handler(newCategory) {
          this.localCategory = { ...newCategory };
        },
        deep: true,
      },
    },
    methods: {
      close() {
        this.isOpen = false;
        this.$emit('update:modelValue', false);
      },
      async activar() {
        await this.$store.dispatch('category/enabledCategory', this.localCategory.pK_CATEGORY);
        this.close();
      },
      async desactivar() {
        
        await this.$store.dispatch('category/disabledCategory', this.localCategory.pK_CATEGORY);
        this.close();
      },
    },
  };
  </script>