<template>
    <v-data-table
      :headers="headers"
      :items="stores"
      :search="search"
      :items-per-page-text="pages"
      :items-per-page-options="[5, 10, 20]"
      :items-per-page="5"
      :loading="loading"
      loading-text="Cargando... Espere por favor"
      page-text="{0}-{1} de {2}"
    >
      <template v-slot:item="{ item }">
        <tr>
          <td>{{ item.storE_NAME }}</td>
          <td>{{ item.manager }}</td>
          <td>{{ item.address }}</td>
          <td>{{ item.phone }}</td>
          <td>{{ item.city }}</td>
          <td>{{ item.email }}</td>
          <td>{{ item.type }}</td>
          <td>{{ item.status }}</td>
          <td>
            <v-icon class="me-2" size="small" @click="editStore(item)">mdi-pencil</v-icon>
            <template v-if="item.status == 'INACTIVO'">
              <v-icon size="small" @click="openModal(item, 1)">mdi-check</v-icon>
            </template>
            <template v-if="item.status == 'ACTIVO'">  
              <v-icon size="small" @click="openModal(item, 2)">mdi-cancel</v-icon>
            </template>
          </td>
        </tr>
      </template>
      <template v-slot:top>
        <v-toolbar>
          <v-toolbar-title>Gestión de Tiendas</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-text-field
            append-inner-icon="mdi-magnify"
            density="compact"
            label="Búsqueda"
            variant="solo"
            hide-details
            single-line
            v-model="search"
            @click:append-inner="fetchStore"
            class="elevation-1"
          ></v-text-field>
          <v-card-actions>
            <v-btn @click="openForm" color="primary" size="large"> Agregar </v-btn>
          </v-card-actions>
        </v-toolbar>
      </template>
      <template v-slot:no-data>
        <v-btn color="primary" @click="initialize"> Resetear </v-btn>
      </template>
    </v-data-table>
    
    <StoreForm v-model="form" :store="selectedStore" @saved="fetchStores" />
    <StoreModal v-model="modal" :store="selectedStore" :action="action" @update:modelValue="modal = $event"  />
  </template>
  
  <script>
  import { mapGetters, mapActions } from 'vuex';
  import StoreForm from './StoreForm.vue';
  import StoreModal from './StoreModal.vue';
  
  export default {
    components: {
      StoreForm,
      StoreModal,
    },
    data() {
      return {
        items: [5, 10, 25],
        pages: "Tiendas por Página",
        search: null,
        form: false,
        modal: false,
        selectedStore: null,
        action: 0,
      };
    },
    computed: {
      ...mapGetters('store', ['stores', 'loading']),
      headers() {
        return [
          { title: 'Tienda', key: 'storE_NAME' },
          { title: 'Encargado', key: 'manager', sortable: false },
          { title: 'Dirección', key: 'address', sortable: false },
          { title: 'Teléfono', key: 'phone', sortable: false },
          { title: 'Ciudad', key: 'city', sortable: false },
          { title: 'Correo', key: 'email', sortable: false },
          { title: 'Tipo', key: 'type', sortable: false },
          { title: 'Estado', key: 'status', sortable: false },
          { title: 'Acciones', key: 'actions', sortable: false },
        ];
      },
    },
    methods: {
      initialize() {
        this.$store.dispatch('store/fetchStores');
        this.search= null;
      },
      ...mapActions('store', ['fetchStores']),
      openForm() {
        this.selectedStore = { pK_STORE: null, storE_NAME: '', manager: '', address: '', phone: '', city: '', email: '' };
        this.form = true;
      },
      editStore(store) {
        this.selectedStore = { ...store };
        this.form = true;
      },
      openModal(store, action) {
        this.selectedStore = { ...store };
        this.action = action;
        this.modal = true;
      },
      fetchStore(){
        this.$store.dispatch('store/fetchStore', this.search);
      },
    },
    mounted() {
      this.fetchStores();
    },
  };
  </script>