import axios from 'axios';

const state = {
  categories: [],
  selectedCategory: null,
  loading: false,
  error: null,
};

const mutations = {
  SET_CATEGORIES(state, categories) {
    state.categories = categories;
  },
  SET_SELECTED_CATEGORY(state, category) {
    state.selectedCategory = category;
  },
  SET_LOADING(state, loading) {
    state.loading = loading;
  },
  SET_ERROR(state, error) {
    state.error = error;
  },
};

const actions = {
  async fetchCategories({ commit }) {
    commit('SET_LOADING', true);
    try {
      const response = await axios.get('api/Categories/ReadCategories');
      commit('SET_CATEGORIES', response.data);
    } catch (error) {
      commit('SET_ERROR', error.message);
    } finally {
      commit('SET_LOADING', false);
    }
  },
  async fetchCategory({ commit }, text) {
    commit('SET_LOADING', true);
    try {
      const response = await axios.get(`api/Categories/ReadCategory/${text}`);
      commit('SET_CATEGORIES', response.data);
    } catch (error) {
      commit('SET_ERROR', error.message);
    } finally {
      commit('SET_LOADING', false);
    }
  },
  async createCategory({ dispatch }, category) {
    await axios.post('api/Categories/CreateCategory', category);
    dispatch('fetchCategories');
  },
  async updateCategory({ dispatch }, category) {
    await axios.put('api/Categories/UpdateCategory', category);
    dispatch('fetchCategories');
  },
  async enabledCategory({ dispatch }, id) {
    await axios.put(`api/Categories/EnabledCategory/${id}`);
    dispatch('fetchCategories');
  },
  async disabledCategory({ dispatch }, id) {
    await axios.put(`api/Categories/DisabledCategory/${id}`);
    dispatch('fetchCategories');
  },
};

const getters = {
  categories: (state) => state.categories,
  selectedCategory: (state) => state.selectedCategory,
  loading: (state) => state.loading,
  error: (state) => state.error,
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
};
