import axios from 'axios';

const state = {
  stores: [],
  selectedStore: null,
  loading: false,
  error: null,
};

const mutations = {
  SET_STORES(state, stores) {
    state.stores = stores;
  },
  SET_SELECTED_STORE(state, store) {
    state.selectedStore = store;
  },
  SET_LOADING(state, loading) {
    state.loading = loading;
  },
  SET_ERROR(state, error) {
    state.error = error;
  },
};

const actions = {
  async fetchStores({ commit }) {
    commit('SET_LOADING', true);
    try {
      const response = await axios.get('api/Stores/ReadStores');
      commit('SET_STORES', response.data);
    } catch (error) {
      commit('SET_ERROR', error.message);
    } finally {
      commit('SET_LOADING', false);
    }
  },
  async fetchStore({ commit }, text) {
    commit('SET_LOADING', true);
    try {
      const response = await axios.get(`api/Stores/ReadStore/${text}`);
      commit('SET_STORES', response.data);
    } catch (error) {
      commit('SET_ERROR', error.message);
    } finally {
      commit('SET_LOADING', false);
    }
  },
  async createStore({ dispatch }, store) {
    await axios.post('api/Stores/CreateStore', store);
    dispatch('fetchStores');
  },
  async updateStore({ dispatch }, store) {
    await axios.put('api/Stores/UpdateStore', store);
    dispatch('fetchStores');
  },
  async enabledStore({ dispatch }, id) {
    await axios.put(`api/Stores/EnabledStore/${id}`);
    dispatch('fetchStores');
  },
  async disabledStore({ dispatch }, id) {
    await axios.put(`api/Stores/DisabledStore/${id}`);
    dispatch('fetchStores');
  },
};

const getters = {
  stores: (state) => state.stores,
  selectedStore: (state) => state.selectedStore,
  loading: (state) => state.loading,
  error: (state) => state.error,
};

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
};
