<template>
    <v-data-table
      :headers="headers"
      :items="categories"
      :search="search"
      :items-per-page-text="pages"
      :items-per-page-options="[5, 10, 20]"
      :items-per-page="5"
      :loading="loading"
      loading-text="Cargando... Espere por favor"
      page-text="{0}-{1} de {2}"
    >
      <template v-slot:item="{ item }">
        <tr>
          <td>{{ item.categorY_NAME }}</td>
          <td>{{ item.status }}</td>
          <td>
            <v-icon class="me-2" size="small" @click="editCategory(item)">mdi-pencil</v-icon>
            <template v-if="item.status == 'INACTIVO'">
              <v-icon size="small" @click="openModal(item, 1)">mdi-check</v-icon>
            </template>
            <template v-if="item.status == 'ACTIVO'">  
              <v-icon size="small" @click="openModal(item, 2)">mdi-cancel</v-icon>
            </template>
          </td>
        </tr>
      </template>
      <template v-slot:top>
        <v-toolbar>
          <v-toolbar-title>Gestión de Categorías</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-text-field
            append-inner-icon="mdi-magnify"
            density="compact"
            label="Búsqueda"
            variant="solo"
            hide-details
            single-line
            v-model="search"
            @click:append-inner="fetchCategory"
            class="elevation-1"
          ></v-text-field>
          <v-card-actions>
            <v-btn @click="openForm" color="primary" size="large"> Agregar </v-btn>
          </v-card-actions>
        </v-toolbar>
      </template>
      <template v-slot:no-data>
        <v-btn color="primary" @click="initialize"> Resetear </v-btn>
      </template>
    </v-data-table>
    
    <CategoryForm v-model="form" :category="selectedCategory" @saved="fetchCategories" />
    <CategoryModal v-model="modal" :category="selectedCategory" :action="action" @update:modelValue="modal = $event"  />
  </template>
  
  <script>
  import { mapGetters, mapActions } from 'vuex';
  import CategoryForm from './CategoryForm.vue';
  import CategoryModal from './CategoryModal.vue';
  
  export default {
    components: {
      CategoryForm,
      CategoryModal,
    },
    data() {
      return {
        items: [5, 10, 25],
        pages: "Categorías por Página",
        search: null,
        form: false,
        modal: false,
        selectedCategory: null,
        action: 0, // 1 for activate, 2 for deactivate
      };
    },
    computed: {
      ...mapGetters('category', ['categories', 'loading']),
      headers() {
        return [
          { title: 'Categoría', key: 'categorY_NAME' },
          { title: 'Estado', key: 'status', sortable: false },
          { title: 'Acciones', key: 'actions', sortable: false },
        ];
      },
    },
    methods: {
      initialize() {
        this.$store.dispatch('category/fetchCategories');
        this.search= null;
      },
      ...mapActions('category', ['fetchCategories']),
      openForm() {
        this.selectedCategory = { pK_CATEGORY: null, categorY_NAME: '' };
        this.form = true;
      },
      editCategory(category) {
        this.selectedCategory = { ...category };
        this.form = true;
      },
      openModal(category, action) {
        this.selectedCategory = { ...category };
        this.action = action;
        this.modal = true;
      },
      fetchCategory(){
        this.$store.dispatch('category/fetchCategory', this.search);
      },
    },
    mounted() {
      this.fetchCategories();
    },
  };
  </script>