<template>
  <v-dialog v-model="isOpen" max-width="400px">
    <v-card>
      <v-card-title>
        <span>{{ localBrand.pK_BRAND ? 'Editar Marca' : 'Agregar Marca' }}</span>
      </v-card-title>
      <v-card-text>
        <v-form ref="form" v-model="valid">
          <v-container>
            <v-row>
              <v-col cols="12" md="12" lg="12" xl="12">
                <v-text-field color="primary" variant="underlined" v-model="localBrand.branD_NAME"
                  :rules="[rules.required]" :counter="40" @keyup="uppercase" label="Nombre de la Marca" required />
              </v-col>
            </v-row>
          </v-container>
        </v-form>
      </v-card-text>
      <v-col xs12 sm12 md12 lg12 xl12>
        <v-card-actions>
          <v-btn color="blue" dark class="mb-2" @click="saveBrand" :disabled="!valid">Guardar</v-btn>
          <v-btn color="red" dark class="mb-2" @click="close">Cancelar</v-btn>
        </v-card-actions>
      </v-col>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: {
    modelValue: {
      type: Boolean,
      required: true,
    },
    brand: {
      type: Object,
      default: () => ({ pK_BRAND: null, branD_NAME: ''}),
    },
  },
  data() {
    return {
      isOpen: this.modelValue,
      valid: false,
      localBrand: { ...this.brand }, // Copia local del objeto brand
      rules: {
        required: (value) => !!value || 'Este campo es requerido.',
      },
    };
  },
  watch: {
    modelValue(newValue) {
      this.isOpen = newValue;
    },
    isOpen(newValue) {
      this.$emit('update:modelValue', newValue);
    },
    brand: {
      handler(newBrand) {
        this.localBrand = { ...newBrand }; // Actualiza la copia local cuando cambia el prop
      },
      deep: true,
    },
  },
  methods: {
    uppercase() {
      this.localBrand.branD_NAME = this.localBrand.branD_NAME.toUpperCase();
    },
    close() {
      this.isOpen = false;
    },
    async saveBrand() {
      if (this.$refs.form.validate()) {
        try {
          if (this.localBrand.pK_BRAND) {
            // Si hay un ID, actualiza la marca
            await this.$store.dispatch('brand/updateBrand', { ...this.localBrand });
          } else {
            // Si no hay ID, crea una nueva marca
            await this.$store.dispatch('brand/createBrand', { ...this.localBrand });
          }
          this.$emit('saved', { ...this.localBrand }); // Emitir la copia local
          this.close();
        } catch (error) {
          console.error('Error al guardar la marca:', error);
          // Aquí puedes mostrar un mensaje de error al usuario si lo deseas
        }
      }
    },
  },
};
</script>