<template>
    <v-dialog v-model="isOpen" max-width="400px">
      <v-card>
        <v-card-title class="headline" v-if="action === 1">Activar Item?</v-card-title>
        <v-card-title class="headline" v-if="action === 2">Desactivar Item?</v-card-title>
        <v-card-text>
          Estás a punto de <span v-if="action === 1">Activar</span>
          <span v-if="action === 2">Desactivar</span> el ítem {{ localStore.storE_NAME }}.
        </v-card-text>
        <v-card-actions>
          <v-btn v-if="action === 1" color="blue" dark class="mb-2" @click="activar">Activar</v-btn>
          <v-btn v-if="action === 2" color="orange" dark class="mb-2" @click="desactivar">Desactivar</v-btn>
          <v-btn color="red" @click="close">Cancelar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </template>
  
  <script>
  export default {
    props: {
      modelValue: {
        type: Boolean,
        required: true,
      },
      store: {
        type: Object,
        default: () => ({ pK_STORE: null, storE_NAME: '', manager: '', address: '', phone: '', city: '', email: '' }),
      },
      action: {
        type: Number,
        required: true,
      },
    },
    data() {
      return {
        isOpen: this.modelValue,
        valid: false,
        localStore: { ...this.store },
      };
    },
    watch: {
      modelValue(newValue) {
        this.isOpen = newValue;
      },
      store: {
        handler(newStore) {
          this.localStore = { ...newStore };
        },
        deep: true,
      },
    },
    methods: {
      close() {
        this.isOpen = false;
        this.$emit('update:modelValue', false);
      },
      async activar() {
        await this.$store.dispatch('store/enabledStore', this.localStore.pK_STORE);
        this.close();
      },
      async desactivar() {
        
        await this.$store.dispatch('store/disabledStore', this.localStore.pK_STORE);
        this.close();
      },
    },
  };
  </script>